import url from "../../../bin/url";
export default {
  props: [
      
  ],
  data() {
    return {
      key: "value",
      goodsList:''
    };
  },
  methods: {
    getGoodsList() {
      let _obj = {
        openId: url.openId,
        id: '',
        size: '10',
        current: '1'
      };
      this.$fetch.post(url.getGoodsList, _obj).then(
        data => {
          console.log(data)
          if (data.code == 0) {
          this.goodsList = data.obj
           this.goodsList.forEach(item => {
              item.picId = url.imgUrl + item.picId;
              console.log(item.picId)
            });
          }
        },
        err => {
          alert("网络缓慢。。");
        }
      );
    }
  },
  mounted() {
    this.getGoodsList();//获取全部列表
  }
};



